import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Card, { CardActions, CardContent, CardMedia } from 'material-ui/Card';
import Typography from 'material-ui/Typography';
import Divider from 'material-ui/Divider';


const styles = {
  card: {
    maxWidth: 300,
    margin: '30px 20px',
    borderRadius: '5px',
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  fontText: {
    color: '#40e0d0',
    textAlign: 'right',
  }
};

function Cards(props) {
  const { classes, store } = props;

  return (
    <div>
      <Card className={classes.card}>
        <CardMedia
          className={classes.media}
          image= {store.showBridge === undefined ? "http://res.cloudinary.com/bguggie/image/upload/v1425514736/place_holder_zuvywg.png" : "http://res.cloudinary.com/bguggie/image/upload/v1425514888/wide_ggbridge_bg_teneax.jpg"} 
          title="Contemplative Reptile"/>
        <CardContent>
          <Typography gutterBottom variant="headline" component="h2">
            {store.Heading}
          </Typography>
          <Typography component="p">
            {store.Subheading}
          </Typography>
          <Divider/>
        </CardContent>
        <CardActions>
        <Typography variant="subheading" gutterBottom className={classes.fontText} align="right">
           {"$" + store.Price}
          </Typography>
        </CardActions>
      </Card>
    </div>
  );
}

Cards.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Cards);


